<template>
    <div class="ticket">
      <p class="centered" style="font-size: 20px">
         
      </p>
      <hr style="margin-top: 0;">
      <table style="border: none;">
          <thead>
              <th class="text-center" style="border: none;"> . : <span> 12 </span> </th>
              <th class="text-center" style="border: none;"> . : <span> 12 </span> </th>
              <th class="text-center" style="border: none;">  : <span>  </span> </th>
          </thead>
      </table>
      <br>
      <table style="border: none;">
          <thead>
              <th class="text-center" style="border: none;">  : <span> {{ current_date }} </span> </th>
              <th class="text-center" style="border: none;">  : <span dir="ltr"> {{ get_current_time }} </span> </th>
          </thead>
      </table>
      <br>
      <table class="text-center">
        <thead>
          <tr>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td> </td>
            <td>5000</td>
            <td>3</td>
            <td>15000</td>
          </tr>
          <tr>
            <td> </td>
            <td>5000</td>
            <td>3</td>
            <td>15000</td>
          </tr>
          <tr>
            <td> </td>
            <td>5000</td>
            <td>3</td>
            <td>15000</td>
          </tr>
        </tbody>
      </table>
      <br>
      <table class="text-center">
          <thead>
              <th class="text-center" style="width: 48%;"></th>
              <th class="text-center">0</th>
          </thead>
          <thead>
              <th class="text-center" style="width: 48%;"></th>
              <th class="text-center">0</th>
          </thead>
          <thead>
              <th class="text-center" style="width: 48%;"> </th>
              <th class="text-center"> {{ total_price | money_filter }} </th>
          </thead>
          <thead>
              <th class="text-center" style="width: 48%;">  </th>
              <th class="text-center"> {{ amount_paid | money_filter }} </th>
          </thead>
          <thead>
              <th class="text-center" style="width: 48%;"></th>
              <th class="text-center"> {{ (amount_paid - total_price) | money_filter }} </th>
          </thead>
      </table>
      <p class="centered">     </p>
    </div>
</template>
<script>
    var d = new Date();
    
    export default {
        data(){
            return {
                current_date:  d.toISOString().split("T")[0],
                total_price: 12500,
                amount_paid: 12500
            }
        },
        methods:{
            print(){
                window.print()
            }
        },
        filters: {
            money_filter: function(value){
                var arr = value.toString().split("").reverse();
                var arr2 = [];
                for(let i=0; i < arr.length; i++){
                    arr2.unshift(arr[i]);
                    if((i + 1) % 3 == 0){
                        arr2.unshift(",");
                    }
                }
                var number_with_comma = arr2.join("");
                if(number_with_comma.charAt(0) == ','){
                    return number_with_comma.slice(1);
                }
                return number_with_comma;
            }
        },
        computed: {
            get_current_time: function(){
                var hour = d.getHours();
                if(hour >= 12){
                    return hour - 12 + " : " + d.getMinutes() + " : " + d.getSeconds() + " PM";
                } 
                return hour + " : " + d.getMinutes() + " : " + d.getSeconds() + " AM";
            }
        }
    }
</script>

<style scoped>
    * {
        font-size: 12px;
        color: black;
    }
    table{
        width: 380px;
    }
    td,
    th,
    tr,
    table {
        border: 1px solid black;
        border-collapse: collapse;
    }

    td.description,
    th.description {
        width: 75px;
        max-width: 75px;
    }

    td.quantity,
    th.quantity {
        width: 40px;
        max-width: 40px;
        word-break: break-all;
    }

    td.price,
    th.price {
        width: 40px;
        max-width: 40px;
        word-break: break-all;
    }

    .centered {
        margin-top: 10px;
        text-align: center;
        align-content: center;
        margin-bottom: 10px;
    }

    .ticket {
        width: 302px;
        max-width: 302px;
    }

</style>